// @ts-nocheck
// 3rd-party libs
import React, { useState, useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
// styles
import styles from './SearchBar.module.css';
// actions
import filterNote from '../../actions/notes/filterNote';

const mapStateToProps = ({ notesStatus }) => ({
    notes: notesStatus.notes
});

const mapDispatchToProps = {
    filterNote
};

const storeEnhancer = connect(
    mapStateToProps,
    mapDispatchToProps
);

const SearchBar: React.FC = (props) => {
    const [inputValue, setInputValue] = useState('');
    const inputText = useRef(null);

    const onInputValueChange = () => {
        setInputValue(inputText.current.value);
        const regex = RegExp(inputText.current.value.toLowerCase(), 'g');
        props.notes.map((note) => {
            if (regex.test(note.text.toLowerCase()) || regex.test(note.title.toLowerCase()) || regex.test(note.author.toLowerCase())) {
                props.filterNote({ id: note.id, isFiltered: true });
                return note;
            }
            props.filterNote({ id: note.id, isFiltered: false });
            return note;
        });
    };

    const onCleanClick = (evt) => {
        evt.preventDefault();
        setInputValue('');
    };

    return (
        <form className={styles.searchBarWrapper}>
            <div className={styles.searchBarButtonLeft}/>
            <Link to={'/search'} className={styles.searchBar}>
                <input
                    className={styles.searchBarInput}
                    type='text'
                    value={inputValue}
                    placeholder='Поиск'
                    onChange={onInputValueChange}
                    ref={inputText}
                />
            </Link>
            <button
                className={styles.searchBarButtonRight}
                onClick={onCleanClick}
            />
        </form>
    );
};

export default storeEnhancer(SearchBar);
