// 3rd-party libs
import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import classNames from 'classnames/bind';
// components
import ArchiveIcon from '../Icons/ArchiveIcon';
import BellIcon from '../Icons/BellIcon';
import TrashIcon from '../Icons/TrashIcon';
import LampIcon from '../Icons/LampIcon';
// styles
import styles from './LeftMenu.module.css';

const cx = classNames.bind(styles);

interface IProps {
    isMenuShown?: boolean;
}

// @ts-ignore
const mapStateToProps = ({ appStatus })=> ({
    isMenuShown: appStatus.isLeftMenuShown
});

const storeEnhancer = connect(
    mapStateToProps,
    null
);

const LeftMenu: React.FC<IProps> = (props) => {
    const menuWrapperStyles = cx({
        leftMenuWrapper: true,
        menuShow: props.isMenuShown,
        menuClose: !props.isMenuShown
    });

    return (
       <div className={menuWrapperStyles}>
           <ul className={styles.menuList}>
               <li className={styles.menuItem}>
                   <Link to='/home' className={styles.menuLink}>
                       <LampIcon fill='#e8e8e8'/>
                       <span className={styles.menuItemText}>
                           Заметки
                       </span>
                   </Link>
               </li>
               <li className={styles.menuItem}>
                   <Link to='/reminders' className={styles.menuLink}>
                       <BellIcon fill='#e8e8e8'/>
                       <span className={styles.menuItemText}>
                           Напоминания
                       </span>
                   </Link>
               </li>
           </ul>
           <ul className={styles.menuList}>
               <li className={styles.menuItem}>
                   <Link to='/archive' className={styles.menuLink}>
                       <ArchiveIcon fill='#e8e8e8'/>
                      <span className={styles.menuItemText}>
                          Архив
                      </span>
                   </Link>
               </li>
               <li className={styles.menuItem}>
                   <Link to='/trash' className={styles.menuLink}>
                       <TrashIcon fill='#e8e8e8'/>
                       <span className={styles.menuItemText}>
                           Корзина
                       </span>
                   </Link>
               </li>
           </ul>
           <div className={styles.menuFooter}>
               Беляев Дмитрий 2019
           </div>
       </div>
    );
};

export default storeEnhancer(LeftMenu);
