// @ts-nocheck
// 3rd-party libs
import React from 'react';
import { connect } from 'react-redux';
import classNames from "classnames/bind";
// styles
import styles from './Trash.module.css';
// components
import NoteItem from '../../components/NoteItem/NoteItem';

const cx = classNames.bind(styles);

interface IProps {
    notes: [];
    isColumn: boolean;
}

const mapStateToProps = ({ appStatus, notesStatus })=> ({
    isColumn: appStatus.isColumnStyle,
    notes: notesStatus.notes
});

const mapDispatchToProps = {

};

const storeEnhancer = connect(
    mapStateToProps,
    mapDispatchToProps
);

const Trash: React.FC<IProps> = (props) => {
    const listStyles = cx({
        notesList: true,
        notesListColumn: props.isColumn
    });

    return (
        <div className={styles.trashWrapper}>
            <h1 className={styles.trashTitle}>
                Корзина
            </h1>
            <div className={listStyles}>
                { props.notes.map((note) =>
                    note.isTrash ? <NoteItem key={note.id} note={note}/> : null
                )}
            </div>
        </div>
    );
};

export default storeEnhancer(Trash);
