// @ts-nocheck
// 3rd-party libs
import React, { useState, useEffect, useRef } from 'react';
import classNames from 'classnames/bind';
import { connect } from 'react-redux';
// components
import ColorItem from '../ColorItem/ColorItem';
// styles
import styles from './NoteItem.module.css';
// helpers
import { colors } from '../../helpers/constants';
// actions
import saveNote from '../../actions/notes/saveNote';
import deleteNote from '../../actions/notes/deleteNote';
import archiveNote from '../../actions/notes/archiveNote';
import totalDelete from '../../actions/notes/totalDelete';
import fixNote from '../../actions/notes/fixNote';
import saveAuthor from '../../actions/notes/saveAuthor';
import addRemind from '../../actions/notes/addRemind';
// helpers
import { addZerosToTime } from '../../helpers/utils';

const cx = classNames.bind(styles);

interface IProps {
    note: {};
    isColumn: boolean;
    saveNote: (note: {}) => void;
    deleteNote: (note: {}) => void;
    archiveNote: (note: {}) => void;
}

const mapStateToProps = ({ appStatus })=> ({
    isColumn: appStatus.isColumnStyle
});

const mapDispatchToProps = {
    saveNote,
    deleteNote,
    archiveNote,
    totalDelete,
    fixNote,
    saveAuthor,
    addRemind
};

const storeEnhancer = connect(
    mapStateToProps,
    mapDispatchToProps
);

const NoteItem: React.FC<IProps> = (props) => {
    const [isNoteHovered, setIsNoteHovered] = useState(false);
    const [isNoteOpened, setIsNoteOpened] = useState(false);
    const [titleValue, setTitleValue] = useState(props.note.title);
    const [textValue, setTextValue] = useState(props.note.text);
    const [colorValue, setColorValue] = useState(props.note.backgroundColor);
    const [picturesValue, setPicturesValue] = useState(props.note.img);
    const [isFasten, setIsFasten] = useState(props.note.isStationary);
    const [isAuthorFormShown, setIsAuthorFormShown] = useState(false);
    const [authorValue, setAuthorValue] = useState(props.note.author);
    const [isFooterShown, setIsFooterShown] = useState(false);
    const [isRemind, setIsRemind] = useState(props.note.isRemind);
    const [dateValue, setDateValue] = useState(props.note.remindedTime.date);
    const [hoursValue, setHoursValue] = useState(props.note.remindedTime.hours);
    const [minutesValue, setMinutesValue] = useState(props.note.remindedTime.minutes);
    const [isRemindFormShow, setIsRemindFormShow] = useState(false);
    const inputText = useRef(null);
    const inputTitle = useRef(null);
    const noteContainer = useRef(null);
    const addAuthorFormContainer = useRef(null);
    const addTimeFormContainer = useRef(null);

    const onInputChange = (evt) => {
        setTitleValue(evt.currentTarget.value);
    };

    const onTextChange = (evt) => {
        setTextValue(evt.currentTarget.value);
    };

    const onInput = (evt) => {
        evt.currentTarget.style.height = (evt.currentTarget.scrollHeight) + 'px';
    };

    useEffect(() => {
        if (!inputTitle.current || !inputText.current) {
            return;
        }
        inputText.current.style.minHeight = (Math.floor(props.note.text.length / 23) * 48) + 'px';
        inputTitle.current.style.minHeight = (Math.floor(props.note.title.length / 23 * 30)) + 'px';
        inputTitle.current.addEventListener('input', onInput);
        inputText.current.addEventListener('input', onInput);
        return function () {
            if (!inputTitle.current) {
                return;
            }
            inputTitle.current.removeEventListener('input', onInput);
            inputText.current.removeEventListener('input', onInput);
        }
    }, [isNoteOpened]);

    useEffect(() => {
        return function () {
            console.log('Сохранилось', props.note);
            props.saveNote(
                {
                    id: props.note.id,
                    text: inputText.current.value,
                    title: inputTitle.current ? inputTitle.current.value : '',
                    backgroundColor: noteContainer.current.style.backgroundColor,
                    isRemind: isRemind,
                    remindedTime: {
                        date: dateValue,
                        hours: hoursValue,
                        minutes: minutesValue
                    },
                    img: picturesValue,
                    author: '',
                    isArchive: false,
                    isTrash: false,
                    isStationary: false
                }
            );
        }
    });

    const onDeleteClick = () => {
        if (props.note.isTrash) {
            props.totalDelete(props.note.id);
            setTextValue('');
            return;
        }
        props.deleteNote({ id: props.note.id, isTrash: true });
    };

    const onReestablishClick = () => {
        props.deleteNote({ id: props.note.id, isTrash: false });
    };

    const onArchiveClick = () => {
        if (props.note.isArchive) {
            props.archiveNote({ id: props.note.id, isArchive: false });
            return;
        }
        props.archiveNote({ id: props.note.id, isArchive: true });
    };

    const onHoverNote = () => {
        setIsNoteHovered(true);
    };

    const onMouseLeave = () => {
        setIsNoteHovered(false);
    };

    const onInputClick = () => {
        setIsNoteOpened(true);
        document.addEventListener('mouseup', onMouseUp);
    };

    const onMouseUp = (evt) => {
        if (!noteContainer.current || noteContainer.current.contains(evt.target) || noteContainer.current === evt.target) {
            return;
        }
        setIsNoteOpened(false);
        document.removeEventListener('mouseup', onMouseUp);
    };

    const onColorClick = (color) => {
        setColorValue(color);
    };

    const noteStyles = cx({
        noteWrapper: true,
        noteWrapperColumn: props.isColumn
    });

    const inputTitleStyles = cx({
        noteInput: true,
        noteTitleInput: true,
    });

    const archiveButtonStyles = cx({
        addArchive: true,
        unArchive: props.note.isArchive
    });

    const fastenButtonStyles = cx({
        fastenButton: true,
        fasted: isFasten
    });

    const createImg = (address) => {
        setPicturesValue(picturesValue.concat(address));
    };

    const onImagesChange = (evt) => {
        const files = Array.from(evt.target.files);
        files.forEach((file) => {
            if (file.type.match('image')) {
                const picReader = new FileReader();
                picReader.addEventListener('load', function (loadEvt) {
                    const picFile = loadEvt.target;
                    createImg(picFile.result);
                });
                picReader.readAsDataURL(file);
            }
        });
    };

    const onFastenClick = () => {
        if (props.note.isStationary) {
            props.fixNote({ id: props.note.id, isStationary: false });
            setIsFasten(false);
            return;
        }
        props.fixNote({ id: props.note.id, isStationary: true });
        setIsFasten(true);
    };

    const addAuthorFormStyles = cx({
        addAuthorForm: true,
        addAuthorFormShow: isAuthorFormShown
    });

    const onAddAuthorClick = () => {
        if (!isAuthorFormShown) {
            document.addEventListener('mousedown', onDocumentMouseDown);
        }
        setIsAuthorFormShown(!isAuthorFormShown);
        setIsFooterShown(!isFooterShown);
    };

    const onAuthorSaveClick = () => {
        props.saveAuthor({ id: props.note.id, author: authorValue });
        setIsAuthorFormShown(false);
        setIsFooterShown(false);
        document.removeEventListener('mousedown', onDocumentMouseDown);
    };

    const onAuthorChange = (evt) => {
        setAuthorValue(evt.target.value);
    };

    const onDocumentMouseDown = (evt) => {
        if (!addAuthorFormContainer.current ||
            addAuthorFormContainer.current.contains(evt.target) ||
            addAuthorFormContainer.current === evt.target) {
            return;
        }
        setIsAuthorFormShown(false);
        setIsFooterShown(false);
        setAuthorValue(props.note.author);
        document.removeEventListener('mousedown', onDocumentMouseDown);
    };

    const onAddTimeClick = () => {
        if (!isRemindFormShow) {
            document.addEventListener('mousedown', onDocumentMouseDownTIME);
        }
        setIsRemindFormShow(!isRemindFormShow);
        setIsFooterShown(!isFooterShown);
    };

    const onDocumentMouseDownTIME = (evt) => {
        if (!addTimeFormContainer.current ||
            addTimeFormContainer.current.contains(evt.target) ||
            addTimeFormContainer.current === evt.target) {
            return;
        }
        setIsRemindFormShow(false);
        setIsFooterShown(false);
        document.removeEventListener('mousedown', onDocumentMouseDownTIME);
        document.removeEventListener('click', onDocumentMouseDownTIME);
    };

    const onTimeSaveClick = (evt) => {
        // evt.preventDefault();
        props.addRemind({ id: props.note.id, date: dateValue, hours: hoursValue, minutes: minutesValue })
    };

    const onDateChange = (evt) => {
        setDateValue(evt.target.value);
    };

    const onHoursChange = (evt) => {
        setHoursValue(evt.target.value);
    };

    const onTimeClick = () => {
        if (!isRemindFormShow) {
            document.addEventListener('click', onDocumentMouseDownTIME);
        }
        setIsRemindFormShow(!isRemindFormShow);
        setIsFooterShown(!isFooterShown);
    };

    const onDeleteTimeClick = () => {
        setDateValue(null);
        setHoursValue(null);
        setMinutesValue(null);
        setIsRemind(false);
        setIsRemindFormShow(false);
        setIsFooterShown(false);
        props.addRemind({ id: props.note.id, date: null, hours: null, minutes: null })
    };

    const onMinutesChange = (evt) => {
        setMinutesValue(evt.target.value);
    };

    const addTimeFormStyles = cx({
        addTimeForm: true,
        addTimeFormShown: isRemindFormShow
    });

    return (
        <div
            style={{ backgroundColor: colorValue }}
            className={noteStyles}
            onMouseEnter={onHoverNote}
            onMouseLeave={onMouseLeave}
            ref={noteContainer}
        >
            <div className={styles.noteRow}>
                <div
                    className={styles.picturesWrapper}
                >
                    { picturesValue.map((pic, i) =>
                        <img key={pic + i} src={pic} width='270'/>
                    )}
                </div>
            </div>
            <div className={styles.noteRow}>
                {
                    (isNoteOpened || !(titleValue.trim() === '')) &&
                        <textarea
                            className={inputTitleStyles}
                            placeholder='Введите заголовок'
                            value={titleValue}
                            onChange={onInputChange}
                            ref={inputTitle}
                        />
                }
            </div>
            <div className={styles.noteRow}>
                <textarea
                    className={styles.noteInput}
                    placeholder='Пустая заметка'
                    value={textValue}
                    ref={inputText}
                    onClick={onInputClick}
                    onChange={onTextChange}
                />
                {
                    (isNoteHovered && !props.note.isTrash) &&
                        <div
                            className={fastenButtonStyles}
                            onClick={onFastenClick}
                        />
                }
            </div>
            {
                props.note.isRemind &&
                    <div
                        className={styles.timeWrapper}
                        onClick={onTimeClick}
                    >
                        Напоминание на &nbsp;
                        <span className={styles.timeText}>{props.note.remindedTime.date}</span> в <span className={styles.timeText}>{addZerosToTime(props.note.remindedTime.hours)}:{addZerosToTime(props.note.remindedTime.minutes)}</span>
                    </div>
            }
            {
                props.note.author &&
                    <div className={styles.noteRow}>
                        <div className={styles.authorWrapper}>
                            <span className={styles.authorTitle}>
                                Автор:
                            </span>
                            <span className={styles.authorResult}>
                                {props.note.author}
                            </span>
                        </div>
                    </div>
            }
            {
                (isNoteHovered || isFooterShown) &&
                    <div className={styles.noteBottomRow}>
                        <div className={styles.leftFooterWrapper}>
                            { !props.note.isTrash ?
                                <>
                                    <div
                                        className={styles.addTimeWrapper}
                                        ref={addTimeFormContainer}
                                    >
                                        <div
                                            className={styles.addTime}
                                            onClick={onAddTimeClick}
                                        />
                                        <form className={addTimeFormStyles}>
                                            <h4 className={styles.addTimeTitle}>
                                                Напоминание
                                            </h4>
                                            <label className={styles.addTimeLabel}>
                                                <p className={styles.addTimeText}>
                                                    Дата:
                                                </p>
                                                <input
                                                    type='date'
                                                    name='date'
                                                    maxLength='8'
                                                    placeholder='Введите дату'
                                                    value={dateValue || props.note.remindedTime.date}
                                                    className={styles.addTimeDateInput}
                                                    onChange={onDateChange}
                                                    required='required'
                                                />
                                            </label>
                                            <p className={styles.addTimeText}>
                                                Время:
                                            </p>
                                            <label>
                                                <input
                                                    type='number'
                                                    placeholder='чч'
                                                    max='24'
                                                    min='0'
                                                    maxLength='2'
                                                    value={hoursValue || props.note.remindedTime.hours}
                                                    className={styles.addTimeInput}
                                                    onChange={onHoursChange}
                                                    required='required'
                                                />
                                                <span className={styles.addTimeSpan}>
                                                    - часов
                                                </span>
                                            </label>
                                            <label>
                                                <input
                                                    type='number'
                                                    placeholder='мм'
                                                    maxLength='2'
                                                    max='59'
                                                    min='0'
                                                    value={minutesValue || props.note.remindedTime.minutes}
                                                    className={styles.addTimeInput}
                                                    onChange={onMinutesChange}
                                                    required='required'
                                                />
                                                <span className={styles.addTimeSpan}>
                                                    - минут
                                                </span>
                                            </label>
                                            <div className={styles.buttonTimeWrapper}>
                                                <div
                                                    className={styles.deleteTimeButton}
                                                    onClick={onDeleteTimeClick}
                                                >
                                                    Удалить
                                                </div>
                                                <input
                                                    type='submit'
                                                    className={styles.addTimeButton}
                                                    onClick={onTimeSaveClick}
                                                    value='Сохранить'
                                                />
                                            </div>
                                        </form>
                                    </div>
                                    <div
                                        className={styles.addAuthorWrapper}
                                        ref={addAuthorFormContainer}
                                    >
                                        <div
                                            className={styles.addAuthor}
                                            onClick={onAddAuthorClick}
                                        />
                                        <div className={addAuthorFormStyles}>
                                            <h4 className={styles.addAuthorTitle}>
                                                Автор заметки:
                                            </h4>
                                            <input
                                                className={styles.addAuthorInput}
                                                type='text'
                                                maxLength='30'
                                                placeholder='Введите автора'
                                                value={authorValue}
                                                onChange={onAuthorChange}
                                            />
                                            <div
                                                className={styles.addAuthorButton}
                                                onClick={onAuthorSaveClick}
                                            >
                                                Сохранить
                                            </div>
                                        </div>
                                    </div>
                                    <div className={styles.addColor}>
                                        <div className={styles.colorList}>
                                            {Object.keys(colors).map((color) => {
                                               return <ColorItem key={color} color={colors[color]} onClick={onColorClick}/>
                                            })}
                                        </div>
                                    </div>
                                    <div className={styles.addPictureWrapper}>
                                        <label className={styles.addPicture}>
                                            <input
                                                className={styles.visuallyHidden} type='file'
                                                onChange={onImagesChange}
                                                multiple
                                            />
                                        </label>
                                    </div>
                                    <div
                                        className={archiveButtonStyles}
                                        onClick={onArchiveClick}
                                    />
                                    <div
                                        className={styles.deleteButton}
                                        onClick={onDeleteClick}
                                    />
                                </> :
                                <>
                                    <div
                                        className={styles.totalDelete}
                                        onClick={onDeleteClick}
                                    />
                                    <div
                                        className={styles.reestablishButton}
                                        onClick={onReestablishClick}
                                    />
                                </>
                            }
                        </div>
                    </div>
            }
        </div>
    );
};

export default storeEnhancer(NoteItem);
