// 3rd-party libs
import React from 'react';
import classNames from "classnames/bind";
import { connect } from 'react-redux';
// styles
import styles from './RightNav.module.css';
// actions
import toggleLayoutStatus from '../../actions/toggleLayoutStatus';

const cx = classNames.bind(styles);

interface IProps {
    isColumnStyle: boolean;
    toggleLayoutStatus: (isColumn: boolean) => void;
}

// @ts-ignore
const mapStateToProps = ({ appStatus })=> ({
    isColumnStyle: appStatus.isColumnStyle
});

const mapDispatchToProps = {
    toggleLayoutStatus
};

const storeEnhancer = connect(
    mapStateToProps,
    mapDispatchToProps
);

const RightNav: React.FC<IProps> = (props) => {
    const buttonStyles = cx({
        layoutButton: true,
        columnStyle: props.isColumnStyle
    });

    const clickHandle = () => {
        props.toggleLayoutStatus(!props.isColumnStyle);
    };

    return (
        <div className={styles.rightNavWrapper}>
            <button
                className={buttonStyles}
                onClick={clickHandle}
            />
        </div>
    );
};

export default storeEnhancer(RightNav);
