// 3rd-party libs
import React from 'react';
import { connect } from 'react-redux'
// actions
import toggleLeftMenuStatus from '../../actions/toggleLeftMenuStatus';
// styles
import styles from './LeftNav.module.css';

interface IProps {
    isMenuShown?: boolean;
    toggleLeftMenuStatus: (status: boolean) => void;
}
// @ts-ignore
const mapStateToProps = ({ appStatus })=> ({
    isMenuShown: appStatus.isLeftMenuShown
});

const mapDispatchToProps = {
    toggleLeftMenuStatus
};

const storeEnhancer = connect(
    mapStateToProps,
    mapDispatchToProps
);

const LeftNav: React.FC<IProps> = (props) => {
    const clickHandler = () => {
        props.toggleLeftMenuStatus(!props.isMenuShown);
    };

    return (
        <div className={styles.leftNav}>
            <div
                className={styles.menuButton}
                onClick={clickHandler}
            />
            <img src='./img/logo.png' className={styles.logo} width='40' height='40' alt='Лого'/>
            <span className={styles.headerTitle}>
                Keep
            </span>
        </div>
    );
};

export default storeEnhancer(LeftNav);
