// interfaces
import { APP_STATUS } from '../types/appStatus';

const initialState = {
    isLeftMenuShown: false,
    isColumnStyle: false
};

const appStatus = (
    state = initialState,
    action: { type: any; payload: any; }
) => {
    switch (action.type) {
        case APP_STATUS.TOGGLE_MENU:
            return {
                ...state,
                isLeftMenuShown: action.payload
            };
        case APP_STATUS.TOGGLE_LAYOUT:
            return {
                ...state,
                isColumnStyle: action.payload
            };
        default:
            return state;
    }
};

export default appStatus;
