// @ts-nocheck
// 3rd-party libs
import React from 'react';
import { connect } from 'react-redux';
import classNames from "classnames/bind";
// components
import AddNoteForm from '../../components/AddNoteForm/AddNoteForm';
import NoteItem from '../../components/NoteItem/NoteItem';
// styles
import styles from './Homepage.module.css';

const cx = classNames.bind(styles);

interface IProps {
    notes: [];
    isColumn: boolean;
}

const mapStateToProps = ({ appStatus, notesStatus })=> ({
    isColumn: appStatus.isColumnStyle,
    notes: notesStatus.notes
});

const mapDispatchToProps = {

};

const storeEnhancer = connect(
    mapStateToProps,
    mapDispatchToProps
);

const Homepage: React.FC<IProps> = (props) => {
    const listStyles = cx({
        notesList: true,
        notesListColumn: props.isColumn
    });

    const wrapperStyles = cx({
        notesWrapper: true,
        notesListColumn: props.isColumn
    });

    return (
        <div className={styles.homepageWrapper}>
            <h1 className={styles.homepageTitle}>
                Заметки
            </h1>
            <AddNoteForm/>
            {
                props.notes.some((note) => {
                    return note.isStationary === true;
                }) &&
                    <div className={wrapperStyles}>
                        <h1 className={styles.listTitle}>
                            Закрепленные
                        </h1>
                        <div className={listStyles}>
                            { props.notes.map((note) =>
                                (note.isTrash || note.isArchive || !note.isStationary) ? null : <NoteItem key={note.id} note={note}/>
                            )}
                        </div>
                    </div>
            }
            <div className={wrapperStyles}>
                {
                    ( props.notes.some((note) => {
                        return note.isStationary === true;
                    }) && props.notes.some((note) => {
                            return (note.isStationary === false && note.isTrash === false && note.isArchive === false);
                        })) &&
                        <h1 className={styles.listTitle}>
                            Другие заметки
                        </h1>
                }
                <div className={listStyles}>
                    { props.notes.map((note) =>
                        (note.isTrash || note.isArchive || note.isStationary) ? null : <NoteItem key={note.id} note={note}/>
                    )}
                </div>
            </div>
        </div>
    );
};

export default storeEnhancer(Homepage);
