// @ts-nocheck
// 3rd-party libs
import React from 'react';
import classNames from 'classnames/bind';
// components
import NoteItem from '../../components/NoteItem/NoteItem';
// styles
import styles from './Search.module.css';
import {connect} from "react-redux";

const cx = classNames.bind(styles);

interface IProps {
    notes: [];
    isColumn: boolean;
}

const mapStateToProps = ({ appStatus, notesStatus })=> ({
    isColumn: appStatus.isColumnStyle,
    notes: notesStatus.notes
});

const mapDispatchToProps = {

};

const storeEnhancer = connect(
    mapStateToProps,
    mapDispatchToProps
);

const Search: React.FC<IProps> = (props) => {
    const listStyles = cx({
        notesList: true,
        notesListColumn: props.isColumn
    });

    return (
        <div className={styles.searchWrapper}>
            <h1 className={styles.searchTitle}>
                Поиск
            </h1>
            {
                !props.notes.some((note) => {
                    return note.isFiltered === true;
                }) &&
                    <div className={styles.notFound}>
                        Не найдено
                    </div>
            }
            <div className={listStyles}>
                { props.notes.map((note) =>
                    note.isFiltered ? <NoteItem key={note.id} note={note}/> : null
                )}
            </div>
        </div>
    );
};

export default storeEnhancer(Search);
