// @ts-nocheck
// interfaces
import { NOTE } from '../types/notes';
// helpers
import { mockNotes } from '../helpers/mocks';

const notes = localStorage.getItem('Notes list') ? JSON.parse(localStorage.getItem('Notes list')) : mockNotes;

const initialState = {
    notes
};

const notesStatus = (
    state = initialState,
    action: { type: any; payload: any; }
) => {
    switch (action.type) {
        case NOTE.ADD:
            try {
                localStorage.setItem('Notes list', JSON.stringify(state.notes.concat(action.payload)));
            }
            catch (e) {
                alert('ЛОКАЛ СТОРЕДЖ СДОХ УДАЛИ ФОТКИ ПАМЯТЬ НЕ РЕЗИНОВАЯ!!!!!!!!!');
            }
            return {
                ...state,
                notes: [...state.notes, action.payload ]
            };
        case NOTE.SAVE:
            try {
                localStorage.setItem('Notes list', JSON.stringify(state.notes.map((note) => {
                    if (note.id === action.payload.id) {
                        note.text = action.payload.text;
                        note.title = action.payload.title;
                        note.backgroundColor = action.payload.backgroundColor;
                        note.img = action.payload.img;
                    }
                    return note;
                })));
            }
            catch (e) {
                alert('ЛОКАЛ СТОРЕДЖ СДОХ УДАЛИ ФОТКИ ПАМЯТЬ НЕ РЕЗИНОВАЯ!!!!!!!!!');
            }
            return {
                ...state,
                notes: [...state.notes.map((note) => {
                    if (note.id === action.payload.id) {
                        note.text = action.payload.text;
                        note.title = action.payload.title;
                        note.backgroundColor = action.payload.backgroundColor;
                        note.img = action.payload.img;
                    }
                    return note;
                })],
            };
        case NOTE.DELETE:
            localStorage.setItem('Notes list', JSON.stringify(state.notes.map((note) => {
                if (note.id === action.payload.id) {
                    note.isTrash = action.payload.isTrash;
                    note.isArchive = false;
                    note.isStationary = false;
                }
                return note;
            })));
            return {
                ...state,
                notes: [...state.notes.map((note) => {
                    if (note.id === action.payload.id) {
                        note.isTrash = action.payload.isTrash;
                        note.isArchive = false;
                        note.isStationary = false;
                    }
                    return note;
                })],
            };
        case NOTE.ARCHIVE:
            localStorage.setItem('Notes list', JSON.stringify(state.notes.map((note) => {
                if (note.id === action.payload.id) {
                    note.isArchive = action.payload.isArchive;
                    note.isStationary = false;
                }
                return note;
            })));
            return {
                ...state,
                notes: [...state.notes.map((note) => {
                    if (note.id === action.payload.id) {
                        note.isArchive = action.payload.isArchive;
                        note.isStationary = false;
                    }
                    return note;
                })],
            };
        case NOTE.FIX:
            localStorage.setItem('Notes list', JSON.stringify(state.notes.map((note) => {
                if (note.id === action.payload.id) {
                    note.isStationary = action.payload.isStationary;
                    note.isArchive = false;
                }
                return note;
            })));
            return {
                ...state,
                notes: [...state.notes.map((note) => {
                    if (note.id === action.payload.id) {
                        note.isStationary = action.payload.isStationary;
                        note.isArchive = false;
                    }
                    return note;
                })],
            };
        case NOTE.TOTAL_DELETE:
            let index = 0;
            state.notes.map((note) => {
                if (note.id === action.payload) {
                    index = state.notes.indexOf(note);
                }
                return note;
            });
            state.notes.splice(index, 1);
            localStorage.setItem('Notes list', JSON.stringify(state.notes));
            return {
                ...state
            };
        case NOTE.FILTER:
            return {
                ...state,
                notes: [...state.notes.map((note) => {
                    if (note.id === action.payload.id && !note.isTrash) {
                        note.isFiltered = action.payload.isFiltered;
                    }
                    return note;
                })],
            };
        case NOTE.SAVE_AUTHOR:
            localStorage.setItem('Notes list', JSON.stringify(state.notes.map((note) => {
                if (note.id === action.payload.id) {
                    note.author = action.payload.author;
                }
                return note;
            })));
            return {
                ...state,
                notes: [...state.notes.map((note) => {
                    if (note.id === action.payload.id) {
                        note.author = action.payload.author;
                    }
                    return note;
                })],
            };
        case NOTE.ADD_REMIND:
            localStorage.setItem('Notes list', JSON.stringify(state.notes.map((note) => {
                if (note.id === action.payload.id) {
                    note.isRemind = true;
                    note.remindedTime.date = action.payload.date;
                    note.remindedTime.hours = action.payload.hours;
                    note.remindedTime.minutes = action.payload.minutes;
                }
                return note;
            })));
            return {
                ...state,
                notes: [...state.notes.map((note) => {
                    if (note.id === action.payload.id) {
                        note.isRemind = true;
                        note.remindedTime.date = action.payload.date;
                        note.remindedTime.hours = action.payload.hours;
                        note.remindedTime.minutes = action.payload.minutes;
                        if (!action.payload.date) {
                            note.isRemind = false;
                        }
                    }
                    return note;
                })],
            };
        default:
            return state;
    }
};

export default notesStatus;
