// @ts-nocheck
// 3rd-party libs
import React from 'react';
import classNames from 'classnames/bind';
import { connect } from 'react-redux';
// components
import AddNoteForm from '../../components/AddNoteForm/AddNoteForm';
// styles
import styles from './Reminders.module.css';
import NoteItem from "../../components/NoteItem/NoteItem";

const cx = classNames.bind(styles);

interface IProps {
    notes: [];
    isColumn: boolean;
}

const mapStateToProps = ({ appStatus, notesStatus })=> ({
    isColumn: appStatus.isColumnStyle,
    notes: notesStatus.notes
});

const mapDispatchToProps = {

};

const storeEnhancer = connect(
    mapStateToProps,
    mapDispatchToProps
);

const Reminders: React.FC<IProps> = (props) => {
    const listStyles = cx({
        notesList: true,
        notesListColumn: props.isColumn
    });

    return (
        <div className={styles.remindersWrapper}>
            <h1 className={styles.remindersTitle}>
                Напоминания
            </h1>
            <AddNoteForm/>
            <div className={listStyles}>
                { props.notes.map((note) =>
                    note.isRemind ? <NoteItem key={note.id} note={note}/> : null
                )}
            </div>
        </div>
    );
};

export default storeEnhancer(Reminders);
